
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
}
body {margin: 0;
}
.nav {
    display: table-cell;
    vertical-align: middle;
    background-color: white;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    margin-top: 0;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.2);
            box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.2);
    margin-bottom: 40px
}
ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: left;
    list-style-type: none;
    margin: 0;
    padding: 20px;
    overflow: hidden;
    background-color: white;
    color: gray;
}
ul:first-child {
  vertical-align: middle;
}
li {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    float: left;
}
li img {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    float: left;
    max-width: 150px;
    vertical-align: middle;
    padding: 0;
    margin-bottom: 10px
}
li a {
    color: gray;
    text-decoration: none;
    vertical-align: middle;
}
li a:hover {
    background-color: white;
    color: purple
}




